<template>
	<div>
		<div>
			<v-row style="margin:auto 10px;">
				<v-col cols="4" v-for="(wishjoin,index) in wishjoinlist" :key="index">
					<v-card>
						<v-row>
							<v-col cols="4">
								<v-img
							:src="url + wishjoin.profileImg"
							height="100px"
							width="100px"
							></v-img>
							</v-col>
							<v-col cols="8">
								<p>{{wishjoin.userName}}({{wishjoin.userId}})</p>
								<p>{{wishjoin.birthDate | birth_sort}}</p>
								<v-btn @click="acceptjoin(wishjoin.studentNo)">승인</v-btn>
								<v-btn @click="rejectjoin(wishjoin.studentNo)">거절</v-btn>
							</v-col>
						</v-row>
					</v-card>
				</v-col>
			</v-row>	
		</div>
	</div>
</template>

<script>
import axios from 'axios'
export default {
	data(){
		return{
			classInfo : [],
			wishjoinlist : []
		}
	},
	created() {
		
		this.get_wantjoin()
	},
	methods : {
		get_wantjoin() {
			axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
			this.classInfo = Response.data
			if(this.classInfo.userNo == this.userInfo.userNo){ //관리자일때
                axios.get(`class/callwaitclass/`+this.$route.params.classno)
                .then((res)=>{
                    this.wishjoinlist = res.data
                })
                .catch((err)=>{
                    err
                })
            }
        })
        .catch((error)=>{
            error
        })
		},
		acceptjoin(userno) {
            let data = {
                classNo : this.$route.params.classno,
                studentNo : userno,
                isApproved : 1
            }
            axios.put('class/classjoin',data)
            .then((Response)=>{
                Response
				alert("가입 수락 완료.");
				this.$router.go()
            })
        },
        rejectjoin(userno){
			let data = {
				classNo : this.$route.params.classno,
                studentNo : userno,
                isApproved : 0
            }
            axios.put('class/classjoin',data)
            .then((Response)=>{
				Response
                alert("가입 거절 완료.");
				this.$router.go()
            })
        }
	},
	computed : {
		userInfo(){
			return this.$store.getters.userInfo
		},
		url(){
			return this.$store.getters.BACKEND_URL
		}
	},
	filters : {
		birth_sort(date){
			date = date.toString()
			return date.substring(0,10)
		}
	}
}
</script>

